import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import styles from "./HotelContainer.module.css";

type HotelContainerType = {
  hotelImageUrl?: string;
  hotelName?: string;
  hotelPrice?: string;
  hotelReviews?: string;
  hotelRating?: string;
  hotelSecondaryRating?: string;
  hotelImageAltText?: string;

  /** Style props */
  propWidth?: Property.Width;
  propRight?: Property.Right;
};

const HotelContainer: FunctionComponent<HotelContainerType> = ({
  hotelImageUrl,
  hotelName,
  hotelPrice,
  hotelReviews,
  hotelRating,
  hotelSecondaryRating,
  hotelImageAltText,
  propWidth,
  propRight,
}) => {
  const vectorIconStyle: CSS.Properties = useMemo(() => {
    return {
      width: propWidth,
      right: propRight,
    };
  }, [propWidth, propRight]);

  return (
    <div className={styles.hotelCard2}>
      <div className={styles.hotelCard}>
        <img
          className={styles.discoveryShoresImage}
          alt=""
          src={hotelImageUrl}
        />
        <div className={styles.stayDetails}>
          <div className={styles.stayDetailsRows}>
            <div className={styles.storyBeachfrontSuite}>{hotelName}</div>
            <b className={styles.discoveryShores}>{hotelPrice}</b>
            <div className={styles.night}>{hotelReviews}</div>
          </div>
        </div>
        <div className={styles.rating}>
          <div className={styles.reviews}>{hotelRating}</div>
          <div className={styles.parent}>
            <div className={styles.div}>{hotelSecondaryRating}</div>
            <img
              className={styles.vectorIcon}
              alt=""
              src={hotelImageAltText}
              style={vectorIconStyle}
            />
          </div>
        </div>
        <button className={styles.moreDetailsButton}>
          <div className={styles.button}>More details</div>
        </button>
      </div>
    </div>
  );
};

export default HotelContainer;
