import { FunctionComponent, useState } from "react";
import { Autocomplete, TextField, Icon } from "@mui/material";
import { LocalizationProvider, DatePicker } from "@mui/x-date-pickers";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import styles from "./SearchFlightsContainer.module.css";

type SearchFlightsContainerType = {
  searchFlightText?: string;

  /** Action props */
  onFrameButtonClick?: () => void;
};

const SearchFlightsContainer: FunctionComponent<SearchFlightsContainerType> = ({
  searchFlightText,
  onFrameButtonClick,
}) => {
  const [
    selectOutlinedDateTimePickerValue,
    setSelectOutlinedDateTimePickerValue,
  ] = useState<string | null>(null);

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <div className={styles.formInputsRow}>
        <div className={styles.inputsRow}>
          <Autocomplete
            className={styles.selectoutlined}
            disablePortal
            options={[
              "Singapore (SIN)",
              "Sydney (SYD)",
              "Siem Reap (REP)",
              "Shanghai (PVG)",
              "Sanya (SYX)",
            ]}
            renderInput={(params: any) => (
              <TextField
                {...params}
                color="primary"
                label="Departure"
                variant="outlined"
                placeholder=""
                helperText=""
              />
            )}
            defaultValue="Singapore -  Changi (SIN)"
            size="medium"
          />
          <Autocomplete
            className={styles.selectoutlined}
            sx={{ width: "100%" }}
            disablePortal
            options={[
              "Clark (CRK)",
              "Launceston (LST)",
              "Kalibo (KLO)",
              "Colombo CMB",
              "Melbourne (AVV)",
              "Los Angeles (LA)",
            ]}
            renderInput={(params: any) => (
              <TextField
                {...params}
                color="primary"
                label="Arrival"
                variant="outlined"
                placeholder=""
                helperText=""
              />
            )}
            defaultValue="Los Angeles (LA)"
            size="medium"
          />
          <div className={styles.selectoutlined}>
            <DatePicker
              label="Date"
              value={selectOutlinedDateTimePickerValue}
              onChange={(newValue: any) => {
                setSelectOutlinedDateTimePickerValue(newValue);
              }}
              renderInput={(params: any) => (
                <TextField
                  {...params}
                  color="primary"
                  variant="outlined"
                  size="medium"
                  renderInput={{ placeholder: "01/05/2022" }}
                  helperText=""
                  fullWidth
                />
              )}
            />
          </div>
        </div>
        <div className={styles.buttonGroup}>
          <button
            className={styles.searchFlightsButton}
            onClick={onFrameButtonClick}
          >
            <div className={styles.button}>{searchFlightText}</div>
          </button>
        </div>
      </div>
    </LocalizationProvider>
  );
};

export default SearchFlightsContainer;
