import { FunctionComponent } from "react";
import styles from "./RecommendedHolidayCard.module.css";

type RecommendedHolidayCardType = {
  destinationImageUrl?: string;
  destinationName?: string;
  packageDuration?: string;
  packagePrice?: string;
};

const RecommendedHolidayCard: FunctionComponent<RecommendedHolidayCardType> = ({
  destinationImageUrl,
  destinationName,
  packageDuration,
  packagePrice,
}) => {
  return (
    <button className={styles.recCard1}>
      <img className={styles.imageIcon} alt="" src={destinationImageUrl} />
      <div className={styles.holidayDetails}>
        <div className={styles.frameParent}>
          <div className={styles.destinationParent}>
            <div className={styles.destination}>{destinationName}</div>
            <div className={styles.duration}>{packageDuration}</div>
          </div>
          <div className={styles.price}>{packagePrice}</div>
        </div>
      </div>
    </button>
  );
};

export default RecommendedHolidayCard;
