import { FunctionComponent, useState, useCallback } from "react";
import MatterhornPopup from "../components/MatterhornPopup";
import PortalPopup from "../components/PortalPopup";
import { useNavigate } from "react-router-dom";
import LosAngelesContainer from "../components/LosAngelesContainer";
import HotelResultsContainer from "../components/HotelResultsContainer";
import FooterContainer from "../components/FooterContainer";
import styles from "./HotelsPage.module.css";

const HotelsPage: FunctionComponent = () => {
  const [isMatterhornPopupOpen, setMatterhornPopupOpen] = useState(false);
  const navigate = useNavigate();

  const openMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(true);
  }, []);

  const closeMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(false);
  }, []);

  const onExploreTextClick = useCallback(() => {
    navigate("/");
  }, [navigate]);

  const onSearchTextClick = useCallback(() => {
    navigate("/results-page");
  }, [navigate]);

  return (
    <>
      <div className={styles.hotelsPage}>
        <header className={styles.topHeader}>
          <div className={styles.topContainer}>
            <img
              className={styles.fickleflightIcon}
              alt=""
              src="/fickleflight2.svg"
            />
            <div className={styles.navigationRight}>
              <div className={styles.navigationMenu}>
                <div className={styles.explore} onClick={onExploreTextClick}>
                  Explore
                </div>
                <div className={styles.explore} onClick={onSearchTextClick}>
                  Search
                </div>
                <button className={styles.hotels}>Hotels</button>
                <button className={styles.offers}>Offers</button>
              </div>
              <div className={styles.accountSection}>
                <img
                  className={styles.hamburgerMenuIcon}
                  alt=""
                  src="/notification.svg"
                />
                <img
                  className={styles.notificationBellIcon}
                  alt=""
                  src="/notification1.svg"
                />
                <img
                  className={styles.unsplashd1upkifd04aIcon}
                  alt=""
                  src="/top_avatar@2x.png"
                />
              </div>
            </div>
          </div>
        </header>
        <LosAngelesContainer />
        <HotelResultsContainer />
        <FooterContainer
          sectionImageUrl="/newsletter-section-background@2x.png"
          logoImageUrl="/logo.svg"
          socialIconImageUrl="/social-icons.svg"
        />
      </div>
      {isMatterhornPopupOpen && (
        <PortalPopup
          overlayColor="rgba(0, 0, 0, 0.3)"
          placement="Centered"
          onOutsideClick={closeMatterhornPopup}
        >
          <MatterhornPopup onClose={closeMatterhornPopup} />
        </PortalPopup>
      )}
    </>
  );
};

export default HotelsPage;
