import { FunctionComponent } from "react";
import styles from "./HotelCardContainer.module.css";

type HotelCardContainerType = {
  bungalowImageUrl?: string;
  roomType?: string;
  hotelName?: string;
  roomPrice?: string;
  reviewCount?: string;
  reviewRating?: string;
  roomImageUrl?: string;
  hotelGroupImageUrl?: string;

  /** Action props */
  openMatterhornPopup?: () => void;
};

const HotelCardContainer: FunctionComponent<HotelCardContainerType> = ({
  bungalowImageUrl,
  roomType,
  hotelName,
  roomPrice,
  reviewCount,
  reviewRating,
  roomImageUrl,
  hotelGroupImageUrl,
  openMatterhornPopup,
}) => {
  return (
    <div className={styles.hotelCard1}>
      <div className={styles.hotelCard}>
        <img className={styles.imageIcon} alt="" src={bungalowImageUrl} />
        <div className={styles.stayDetails}>
          <div className={styles.stayDetailsRows}>
            <div className={styles.type}>{roomType}</div>
            <b className={styles.title}>{hotelName}</b>
            <div className={styles.price}>{roomPrice}</div>
          </div>
          <button className={styles.video} onClick={openMatterhornPopup}>
            <img className={styles.groupIcon} alt="" src={reviewCount} />
          </button>
        </div>
        <div className={styles.rating}>
          <div className={styles.reviews}>{reviewRating}</div>
          <div className={styles.starsParent}>
            <div className={styles.stars}>{roomImageUrl}</div>
            <img
              className={styles.vectorIcon}
              alt=""
              src={hotelGroupImageUrl}
            />
          </div>
        </div>
        <button className={styles.moreDetailsButton}>
          <div className={styles.button}>More details</div>
        </button>
      </div>
    </div>
  );
};

export default HotelCardContainer;
