import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import styles from "./DestinationContainer.module.css";

type DestinationContainerType = {
  destinationImageUrl?: string;
  destinationName?: string;
  destinationPrice?: string;

  /** Style props */
  propTop?: Property.Top;
  propBottom?: Property.Bottom;
};

const DestinationContainer: FunctionComponent<DestinationContainerType> = ({
  destinationImageUrl,
  destinationName,
  destinationPrice,
  propTop,
  propBottom,
}) => {
  const cardImageIconStyle: CSS.Properties = useMemo(() => {
    return {
      top: propTop,
      bottom: propBottom,
    };
  }, [propTop, propBottom]);

  return (
    <button className={styles.col1}>
      <div className={styles.pariscard}>
        <img
          className={styles.cardimageIcon}
          alt=""
          src={destinationImageUrl}
          style={cardImageIconStyle}
        />
        <div className={styles.destinationDetails}>
          <b className={styles.destination}>{destinationName}</b>
          <div className={styles.details}>
            <div className={styles.from}>from</div>
            <div className={styles.price}>{destinationPrice}</div>
          </div>
        </div>
      </div>
    </button>
  );
};

export default DestinationContainer;
