import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import styles from "./AttractionsCardContainer.module.css";

type AttractionsCardContainerType = {
  attractionIconUrl?: string;
  commuteText?: string;

  /** Style props */
  propBackgroundColor?: Property.BackgroundColor;
};

const AttractionsCardContainer: FunctionComponent<
  AttractionsCardContainerType
> = ({ attractionIconUrl, commuteText, propBackgroundColor }) => {
  const iconStyle: CSS.Properties = useMemo(() => {
    return {
      backgroundColor: propBackgroundColor,
    };
  }, [propBackgroundColor]);

  return (
    <button className={styles.card}>
      <div className={styles.icon} style={iconStyle}>
        <img className={styles.iconVector} alt="" src={attractionIconUrl} />
      </div>
      <div className={styles.text}>{commuteText}</div>
    </button>
  );
};

export default AttractionsCardContainer;
